import styleGradient from './style-gradient/index'
import * as styleTypesAll from './styleTypes';
import loadColorLinear from './colorLinear';
import { typeBgKey } from './utils';

const GRAD_DIRS = ['right', 'bottom', 'left', 'top'];
const GRAD_TYPES = ['linear', 'radial', 'repeating-linear', 'repeating-radial'];
const defaultCpAttr = '[data-toggle="handler-color-wrap"]';
const PROP_GRADIENT = 'background-image-gradient';
const PROP_DIR = `${PROP_GRADIENT}-dir`;
const PROP_TYPE = `${PROP_GRADIENT}-type`;

let styleTypes = { ...styleTypesAll };

const bgTypeIconAttrs = 'style="max-height: 16px; display: block; margin: 0 auto" viewBox="0 0 24 24"';
let PROPERTY_BG_TYPE = typeBgKey;
const PROPERTY_IMAGE = 'background-image';
const PROPERTY_BG_IMAGE = PROPERTY_IMAGE;
const PROPERTY_BG_COLOR = `${PROPERTY_BG_IMAGE}-color`;
const PROPERTY_BG_GRAD = `${PROPERTY_BG_IMAGE}-gradient`;
const PROPERTY_BG_GRAD_DIR = `${PROPERTY_BG_GRAD}-dir`;
const PROPERTY_BG_GRAD_TYPE = `${PROPERTY_BG_GRAD}-type`;

const getValidDir = (value) => {
  return GRAD_DIRS.filter(dir => value.indexOf(dir) > -1)[0];
}

const toGradient = (type, angle, color) => {
  const angles = [...GRAD_DIRS, 'center'];
  let angleValue = angle;

  if (['linear', 'repeating-linear'].indexOf(type) >= 0
    && angles.indexOf(angleValue) >= 0) {
    angleValue = angleValue === 'center' ? 'to right' : `to ${angleValue}`;
  }

  if (['radial', 'repeating-radial'].indexOf(type) >= 0 && angles.indexOf(angleValue) >= 0) {
    angleValue = `circle at ${angleValue}`;
  }

  return color ? `${type}-gradient(${angleValue}, ${color})` : '';
};

const typeName = (name) => `${name}-gradient(`;

const BackgroundType = {
  Image: 'image',
  Color: 'color',
  Grad: 'grad'
};

const getLayerSeparator = () => {
  let sep = '(?<!\\(.*[^)]),';
  return new RegExp(`${sep}(?![^\\(]*\\))`);
}

const getLastStyleValue = (value) => {
  // return isArray(value) ? value[value.length - 1] : value;
  // rewrite the top line
  if (Array.isArray(value)) {
    return value[value.length - 1];
  } else {
    return value;
  }
};

const __splitValue = (value, sep) => {
  return getLastStyleValue(value)
    .split(sep)
    .map(value => value.trim())
    .filter(Boolean);
}

const __splitStyleName = (style, name, sep) => {
  return __splitValue(style[name] || '', sep);
}

/**
 * Parse CSS gradient value.
 */
export const parseGradient = (value) => {
  const start = value.indexOf('(') + 1;
  const end = value.lastIndexOf(')');
  const content = value.substring(start, end);
  const values = content.split(/,(?![^(]*\))/);
  const result = {
    direction: 'left',
    type: 'linear',
    content,
    colors: content,
    stops: [],
  };

  if (!content) {
    return result;
  }

  if (values.length > 2) {
    result.direction = values.shift();
    result.colors = values.join(',').trim();
  }

  let typeFound = false;
  const types = ['repeating-linear', 'repeating-radial', 'linear', 'radial'];
  types.forEach(name => {
    if (value.indexOf(typeName(name)) > -1 && !typeFound) {
      typeFound = true;
      result.type = name;
    }
  });

  result.stops = values.map(value => {
    const parts = value.split(' ');
    const position = (parts.length > 1 ? parts.pop() : '').trim();
    const color = parts.join(' ').trim();
    return { color, position };
  });

  return result;
};


const getLayerFromBgImage = (imagePart) => {

  const result = {
    ['type']: BackgroundType.Image,
  };

  if (imagePart.indexOf('url(') > -1) {
    // Background is Image
    result['value'] = imagePart;
  } else if (imagePart.indexOf('gradient(') > -1) {
    const parsed = parseGradient(imagePart);
    const { stops } = parsed;

    if (
      stops.length === 2
      && parsed.type === 'linear'
      && (stops[0].color === stops[1].color)
    ) {
      // Background is Color
      result['type'] = BackgroundType.Color;
      result['value'] = stops[0].color;
    } else {
      // Background is Gradient
      const gradDir = getValidDir(parsed.direction) || GRAD_DIRS[0];
      const gradType = parsed.type || GRAD_TYPES[0];
      result['type'] = BackgroundType.Grad;
      result['value'] = toGradient(gradType, gradDir, parsed.colors);
    }
  }

  return result;
};

export default (editor, opts = {}) => {
  const options = {
    ...{
      // Options for the `grapesjs-style-gradient` plugin
      styleGradientOpts: {},

      // Extend single style property definition of the plugin.
      // You can this, for example, to change the defauld gradient color
      propExtender: p => p,

      // Use this function to change/add/extend style properties for each BG type
      typeProps: p => p,
    }, ...opts
  };

  let styleTypes = { ...styleTypesAll };
  const sm = editor.StyleManager;
  const stack = sm.getType('stack');
  const propModel = stack.model;
  styleTypes = Object.keys(styleTypes).reduce((acc, item) => {
    const prop = styleTypes[item];
    acc[item] = options.propExtender(prop) || prop;
    return acc;
  }, {});

  const getPropsByType = type => {

    let result = [
      styleTypes.typeImage,
      styleTypes.typeBgRepeat,
      styleTypes.typeBgPos,
      styleTypes.typeBgAttach,
      styleTypes.typeBgSize,
    ];

    switch (type) {
      case 'color':
        result = [styleTypes.typeColorLin]
        break;
      case 'grad':
        result = [styleTypes.typeGradient]
        break;
    }

    return options.typeProps(result, type) || result;
  };

  styleGradient(editor, {
    colorPicker: 'default',
    ...options.styleGradientOpts,
  });

  loadColorLinear(editor, sm);

  sm.addType('bg', {
    model: propModel.extend({
      
      defaults: () => ({
        ...propModel.prototype.defaults,
        detached: 1,
        preview: 1,
        full: 1,
        prepend: 1,
        properties: [
          styleTypes.typeBg,
          ...getPropsByType(),
        ],
      }),

      init() {
        propModel.prototype.init.apply(this, arguments);
        this.handleTypeChange = this.handleTypeChange.bind(this);
        this.listenTo(this.getLayers(), 'add', this.onNewLayerAdd);
      },

      _updateLayerProps(layer, type) {
        const props = layer.get('properties');
        props.remove(props.filter((it, id) => id !== 0));
        let expra_pros = getPropsByType(type);
        expra_pros.forEach(item => props.push(item))
      },

      /**
       * On new added layer we should listen to filter_type change
       * @param  {Layer} layer
       */
      onNewLayerAdd(layer) {
        const typeProp = layer.getPropertyAt(0);
        layer.listenTo(typeProp, 'change:value', this.handleTypeChange)
      },

      handleTypeChange(propType, type, opts) {
        const currLayer = this.getCurrentLayer();
        currLayer && this._updateLayerProps(currLayer, type);
        // opts.fromInput && this.trigger('updateValue');
      },

      getLayersFromTarget(target, { resultValue } = {}) {

        let sep = getLayerSeparator();
        let props = this.get('properties');
        let name = this.get('name');

        let result_layers = [];
        let layers = [];
        let style = resultValue || target.getStyle()[this.get('property')];

        if (style[PROPERTY_IMAGE]) {

          layers = __splitStyleName(
            style, PROPERTY_IMAGE, sep).map(getLayerFromBgImage);

          // Update layers by inner properties
          props.forEach((prop) => {
            const id = prop.getId();
            const propName = prop.getName();
            if (propName === PROPERTY_IMAGE) return;
            let tmp_result = __splitStyleName(style, propName, sep)
            tmp_result = tmp_result.map((value) => ({ [id]: value || prop.getDefaultValue() }))
            tmp_result.forEach((inLayer, i) => {
                layers[i] = layers[i] ? { ...layers[i], ...inLayer } : inLayer;
              });
          });

        } else {
          // Partial support for the `background` property
          style = target.getStyle();
          layers = __splitStyleName(style, name, /(?![^)(]*\([^)(]*?\)\)),(?![^\(]*\))/)
            .map((value) => value.substring(0, value.lastIndexOf(')') + 1))
            .map(getLayerFromBgImage);
        }

        layers.forEach((layer, i) => {
          if (layer.type == BackgroundType.Grad) {
            result_layers.push({
              properties: [
                { ...styleTypes.typeBg, value: layer['type'] },
                { ...styleTypes.typeGradient, value: layer['value'] },
              ]
            });
          } else if (layer.type == BackgroundType.Color) {
            result_layers.push({
              properties: [
                { ...styleTypes.typeBg, value: layer['type'] },
                { ...styleTypes.typeColorLin, value: layer['value'] },
              ]
            });
          } else {
            // deal repeat x and y
            if (layer['background-repeat-x'] == 'no-repeat' && layer['background-repeat-y'] == 'no-repeat') {
              layer['background-repeat'] = 'no-repeat';
            } else if (layer['background-repeat-x'] == 'repeat' && layer['background-repeat-y'] == 'repeat') {
              layer['background-repeat'] = 'repeat';
            } else if (layer['background-repeat-x'] == 'repeat' && layer['background-repeat-y'] == 'no-repeat') {
              layer['background-repeat'] = 'repeat-x';
            } else if (layer['background-repeat-x'] == 'no-repeat' && layer['background-repeat-y'] == 'repeat') {
              layer['background-repeat'] = 'repeat-y';
            }
            // deal background-position
            if (layer['background-position-x'] == 'left' && layer['background-position-y'] == 'top') {
              layer['background-position'] = 'left top';
            } else if (layer['background-position-x'] == 'left' && layer['background-position-y'] == 'center') {
              layer['background-position'] = 'left center';
            } else if (layer['background-position-x'] == 'left' && layer['background-position-y'] == 'bottom') {
              layer['background-position'] = 'left bottom';
            } else if (layer['background-position-x'] == 'right' && layer['background-position-y'] == 'top') {
              layer['background-position'] = 'right top';
            } else if (layer['background-position-x'] == 'right' && layer['background-position-y'] == 'center') {
              layer['background-position'] = 'right center';
            } else if (layer['background-position-x'] == 'right' && layer['background-position-y'] == 'bottom') {
              layer['background-position'] = 'right bottom';
            } else if (layer['background-position-x'] == 'center' && layer['background-position-y'] == 'top') {
              layer['background-position'] = 'center top';
            } else if (layer['background-position-x'] == 'center' && layer['background-position-y'] == 'center') {
              layer['background-position'] = 'center center';
            } else if (layer['background-position-x'] == 'center' && layer['background-position-y'] == 'bottom') {
              layer['background-position'] = 'center bottom';
            }
            result_layers.push({
              properties: [
                { ...styleTypes.typeBg, value: layer['type'] },
                { ...styleTypes.typeImage, value: layer['value'] },
                { ...styleTypes.typeBgRepeat, value: layer['background-repeat']},
                { ...styleTypes.typeBgPos, value: layer['background-position']},
                { ...styleTypes.typeBgAttach, value: layer['background-attachment']},
                { ...styleTypes.typeBgSize, value: layer['background-size']},
              ]
            });
          }
        });
        
        return result_layers;
      }
    }),

    view: stack.view,
  });
};